import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function PauseOnHover() {
  var settings = {
    dots: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    pauseOnHover: true
  };
  return (
    <div className="slider-container m-16 w-xl">
      <Slider {...settings}>
        <div>
          <img src="./asset/eco123.jpg"/>
        </div>
        <div>
        <img src="./asset/discount.jpg"/>
        </div>
        <div>
        <img src="./asset/123.jpg"/>
        </div>
        <div>
        <img src="./asset/sale123.jpg"/>
        </div>
        <div>
        <img src="./asset/8480809.jpg"/>
        </div>
       
      </Slider>
    </div>
  );
}

export default PauseOnHover;
