  export default function Category() {
    const dispatch = useDispatch();
    const [filter, setFilter] = useState(null);
    const handleFilter = (e, option) => {
      setFilter(option);
      console.log("koko");
  
      dispatch(setCategoryAsync(option));
    };
    return (
      <div className="bg-white">
        <div className="mx-auto max-w-2xl px-4 py-16 sm:px-6 sm:py-10 lg:max-w-7xl lg:px-8">
          <h2 className="sr-only">Products</h2>
  
          <div className="grid grid-cols-3 gap-x-24 gap-y-8 sm:grid-cols-3 lg:grid-cols-6 lg:gap-x-24 xl:grid-cols-9 xl:gap-x-12 ">
            {products.map((product) => (
              <div className="">
                <div className="h-auto">
                  <Link
                   to="/Perticular"
                    className=" group flex justify-center items-center"
                    onClick={(e) => handleFilter(e,(product.category))}
                  >
                    <div className=" object-center aspect-h-1 aspect-w-1 w-32 overflow-hidden rounded-full">
                      <img
                        src={product.imageSrc}
                        alt={product.imageAlt}
                        className="h-full w-full object-cover object-center group-hover:opacity-75"
                      />
                    </div>
                  </Link>
                </div>
                <div className="text-center">
                  <p className=" mx-auto text-sm font-bold">{product.name}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }
  