import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { checkUser, fetchUser, signOut, checkAuth, ResetPassword, ResetPasswordRequest } from "./authAPI";
import { updateUser } from "./authAPI";
const initialState = {
  loggedInUserToken: null,
  status: "idle",
  error: null,
  userChecked: false,
  mailsent:false,
  passwordReset:false
};
export const fetchuserAsync = createAsyncThunk(
  "auth/fetchUser",
  async (userData) => {
    const response = await fetchUser(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const checkuserAsync = createAsyncThunk(
  "auth/checkUser",
  async (loginData, { rejectWithValue }) => {
    try {
      console.log(loginData);
      const response = await checkUser(loginData);
   
      return response.data;
    } catch (err) {
      return rejectWithValue(err);
    }
  }
);

export const checkAuthAsync = createAsyncThunk(
  "auth/checkAuth",
  async () => {
    try {
      const response = await checkAuth();
      return response.data;
    } catch (err) {
      
    }
  }
);

export const resetPasswordRequestAsync = createAsyncThunk(
  "auth/ResetPasswordRequest",
  async (email,{rejectWithValue}) => {
    try {
      const response = await ResetPasswordRequest(email);
      return response.data;
    } catch (err) {
      return rejectWithValue(err);
    }
  }
);

export const resetPasswordAsync = createAsyncThunk(
  "auth/ResetPassword",
  async (data,{rejectWithValue}) => {
    try {
      const response = await ResetPassword(data);
      return response.data;
    } catch (err) {
      return rejectWithValue(err);
    }
  }
);

export const updateUserAsync = createAsyncThunk(
  "auth/updateUser",
  async (update) => {
    const response = await updateUser(update);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const signOutAsync = createAsyncThunk("auth/signOut", async () => {
  const response = await signOut();
  // The value we return becomes the `fulfilled` action payload
  return response.data;
});

export const authSlice = createSlice({
  name: "userData",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(fetchuserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchuserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
      })
      .addCase(checkuserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(checkuserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
      })
      .addCase(checkuserAsync.rejected, (state, action) => {
        state.status = "idle";
        state.error = action.payload;
      })
      .addCase(updateUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(updateUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
      })
      .addCase(signOutAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(signOutAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = null;
      })
      .addCase(checkAuthAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
        state.userChecked = true;
      })
      .addCase(checkAuthAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.userChecked = true;
      })  
      .addCase(resetPasswordRequestAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(resetPasswordRequestAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.mailsent = true;
      })
      
      .addCase(resetPasswordAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(resetPasswordAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.passwordReset = true;
      })
      .addCase(resetPasswordAsync.rejected, (state, action) => {
        state.status = "idle";
        state.error = action.payload;
      });
  },
});

export const { increment } = authSlice.actions;

export const selectUser = (state) => state.userData.loggedInUserToken;

export const selectError = (state) => state.userData.error;
export const selectMail=(state)=>state.userData.mailsent;
export const selectUserChecked = (state) => state.userData.userChecked;
export const selectPasswordReset = (state) => state.userData.passwordReset;
export default authSlice.reducer;
