import * as React from "react";
import { selectAllUser, selectUserInfo, setEmailAsync } from "../../user/userSlice";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";

export default function AllUser() {
  const user = useSelector(selectAllUser);
    const dispatch=useDispatch();
    const showOrder=(email)=>{
        dispatch(setEmailAsync(email))
    }
  return (
    <>
    <div className="mx-32 my-8 text-5xl  font-bold text-black">
        All Users :
    </div>
      <div className="mx-40 my-10">
        {user.map((users) => (
            
          <div className="mt-5 flex flex-wrap flex-row border-gray-500 border-2" >
            <div className="m-auto  px-4 py-6 sm:px-6">
              <div>
                <h1 className="flex text-4xl my-5 font-bold tracking-tight text-gray-900 ">
                  Name: {users.name ? users.name : "New User"}
                </h1>
              </div>

              <h3 className="text-xl my-5 font-bold tracking-tight text-red-900">
                Email Address : {users.email}
              </h3>
              {user.role === "admin" && (
                <h3 className="text-xl my-5 font-bold tracking-tight text-red-900">
                  role : {users.role}
                </h3>
              )}
            </div>
            <div className="m-auto border-t border-gray-200 px-3 py-5 sm:px-4">
              <img
                className="h-32 w-32 rounded-full"
                src={`https://ui-avatars.com/api/?name=${users.name}&bold=true&background=random`}
                alt=""
              />
              <div className="m-auto py-5 ">
               <Link to={`/admin/orders?id=${users.id}`}
            className="m-auto rounded-md  bg-green-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-green-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
          >
            Show order
          </Link>
            </div>
            </div>
          </div>
        ))}
      </div>
    </>
  );
}
