import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchAllUserAdmin, fetchAllUserOrders, fetchLoggedInUser, fetchLoggedInUserOrders, setEmail, updateUser } from './userAPI';
import { fetchAllOrders } from '../Order/OrderAPI';

const initialState = {
  userOrders: [],
  AllOrders:[],
  AllUser:[],
  status: 'idle',
  userInfo:null,
  setEmail:null
};

export const fetchLoggedInUserOrderAsync = createAsyncThunk(
  'user/fetchLoggedInUserOrders',
  async () => {
    const response = await fetchLoggedInUserOrders();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const setEmailAsync=createAsyncThunk(
  'product/setEmail',
   (email)=>{
    console.log(email)
    const response= setEmail(email);
    return response.data;
  }
)

export const fetchAllUserOrderAsync = createAsyncThunk(
  'user/fetchAllUserOrders',
  async (id) => {
    const response = await fetchAllUserOrders(id);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchAllUserAsync = createAsyncThunk(
  'user/fetchAllUserAdmin',
  async () => {
    const response = await fetchAllUserAdmin();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchLoggedInUserAsync = createAsyncThunk(
  'user/fetchLoggedInUser',
  async () => {
    const response = await fetchLoggedInUser();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const updateUserAsync = createAsyncThunk(
  'user/updateUser',
  async (id) => {
    const response = await updateUser(id);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchLoggedInUserOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchLoggedInUserOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.userOrders = action.payload;
      })
      .addCase(setEmailAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(setEmailAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.setEmail = action.payload;
      })
      .addCase(fetchAllUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        // this info can be different or more from logged-in User info
        state.AllUser = action.payload;
      })
      .addCase(fetchAllUserOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllUserOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        // this info can be different or more from logged-in User info
        state.AllOrders = action.payload;
      })
       .addCase(updateUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        // this info can be different or more from logged-in User info
        state.userInfo = action.payload;
      }).addCase(fetchLoggedInUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchLoggedInUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        // this info can be different or more from logged-in User info
        state.userInfo = action.payload;
      });
  },
});

export const selectUserOrders = (state)=>state.user.userOrders;
export const selectAllOrders = (state)=>state.user.AllOrders;
export const selectAllUser = (state)=>state.user.AllUser;
export const selectUserInfo = (state)=>state.user.userInfo;
export const selectUserEmail = (state)=>state.user.setEmail;
export const selectUserInfoStatus = (state) => state.user.status;

export default userSlice.reducer;